<template>
  <div id="div">
    <el-form
      :rules=" loginFormRules"
      class="login-container"
      label-position="left"
      label-width="0px"
      :model="loginForm"
      ref="loginFormRef"
    >
      <h3 class="login_title">系统登录</h3>
      <el-form-item prop="username">
        <el-input
          prefix-icon="el-icon-user"
          type="text"
          placeholder="账号"
          v-model="loginForm.username"
        ></el-input>
      </el-form-item>
      <el-form-item prop="password">
        <el-input
          prefix-icon="el-icon-lock"
          type="password"
          placeholder="密码"
          v-model="loginForm.password"
        ></el-input>
      </el-form-item>
      <el-checkbox class="login_remember" label-position="left">记住密码？</el-checkbox>
      <el-form-item class="btns">
        <el-button type="primary" @click="login">登录</el-button>
        <el-button type="info" @click="resetForm">重置</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
export default {
  data() {
    return {
      loginFormRules: {
        username: [
          { required: true, message: "请输入密码", trigger: "blur" },
          { min: 3, max: 12, message: "长度在 3 到 12 个字符", trigger: "blur" }
        ],
        password: [
          { required: true, message: "请输入密码", trigger: "blur" },
          { min: 3, max: 12, message: "长度在 3 到 12 个字符", trigger: "blur" }
        ]
      },
      //   checked: true,
      loginForm: {
        username: "mjy",
        password: "0205"
      }
      // loading: false
    };
  },
  methods: {
    resetForm: function() {
      this.$refs.loginFormRef.resetFields();
    },
    login() {
      this.$refs.loginFormRef.validate(async valid => {
        if (!valid) return;
        let param = new URLSearchParams();
        param.append("username", this.$data.loginForm.username);
        param.append("password", this.$data.loginForm.password);
        await this.$http.post("/hello", param).then(resp=>{
            if(resp.status==200 && resp.data.status==1){
              // console.log(resp);
                window.sessionStorage.setItem("token",resp.headers.authorization);
                this.$router.push("/home");
                return this.$message.success(resp.data.msg)
            }else{
                return this.$message.error(resp.data.msg)
            }
        },resp=>{
            return this.$message.error('未知错误')
        })
        
      });
    }
  }
};
</script>
<style>
.login-container {
  border-radius: 15px;
  background-clip: padding-box;
  /* margin: 180px auto; */
  width: 350px;
  padding: 35px 35px 15px 35px;
  background: #fff;
  border: 1px solid #eaeaea;
  box-shadow: 0 0 25px #cac6c6;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

.login_title {
  margin: 0px auto 40px auto;
  text-align: center;
  color: #505458;
}

.login_remember {
  margin: 0px 0px 10px 0px;
  text-align: left;
}

.btns {
  margin-left: 25%;
}

#div {
  background-color: rgb(199, 231, 204);
  height: 100%;
}
</style>