<template>
  <div>
    <el-card class="box-card">
      <el-row :gutter="10">
        <el-col :span="8">
          <el-input placeholder="请输入内容" v-model="searchname" class="input-with-select">
            <el-button slot="append" icon="el-icon-search" @click="searchByName"></el-button>
          </el-input>
        </el-col>
        <el-col :span="4">
          <el-button type="primary" @click="dialogFormVisible=true">添加用户</el-button>
        </el-col>
      </el-row>
      <el-table :data="userlist" border style="width: 100%" :stripe="true" v-loading="tableLoading">
        <el-table-column fixed prop="id" label="ID" width="100" align="center"></el-table-column>
        <el-table-column prop="username" label="姓名" width="200" align="center"></el-table-column>
        <el-table-column prop="password" label="密码" width="250" align="center"></el-table-column>
        <el-table-column prop="email" label="邮箱" width="300" align="center"></el-table-column>
        <el-table-column prop="create_time" label="账号创建时间" width="300" align="center"></el-table-column>
        <el-table-column prop="status" label="账号状态" width="80" align="center">
          <template slot-scope="scope">
            <el-switch
              v-model="scope.row.status"
              active-color="#13ce66"
              inactive-color="#ff4949"
              @change="statusChange(scope.row.status,scope.row.id)"
            ></el-switch>
          </template>
        </el-table-column>
        <el-table-column prop="roles" label="权限" width="100" align="center">
          <template slot-scope="scope">
            <div v-for="role in scope.row.roles" :key="role.id">
              <span>{{role.role_des}}</span>
            </div>
          </template>
        </el-table-column>
        <el-table-column fixed="right" label="操作" width="180" align="center">
          <template slot-scope="scope">
            <el-tooltip
              class="item"
              effect="dark"
              content="编辑用户"
              placement="top"
              :enterable="false"
            >
              <el-button
                type="primary"
                icon="el-icon-edit"
                circle
                @click="showUpdateUser(scope.row.id)"
              ></el-button>
            </el-tooltip>

            <el-tooltip
              class="item"
              effect="dark"
              content="用户权限"
              placement="top"
              :enterable="false"
            >
              <el-button
                type="primary"
                icon="el-icon-user-solid"
                circle
                @click="showEditRole(scope.row.id)"
              ></el-button>
            </el-tooltip>

            <el-tooltip
              class="item"
              effect="dark"
              content="删除用户"
              placement="top"
              :enterable="false"
            >
              <el-button type="danger" icon="el-icon-delete" circle @click="delUser(scope.row.id)"></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page.sync="pagenum"
        :page-sizes="[1, 2, 3, 4, 5]"
        :page-size="pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="totalpage"
      ></el-pagination>
    </el-card>

    <!-- 添加用户界面 -->
    <el-dialog title="添加用户" :visible.sync="dialogFormVisible" width="50%" @close="addclose">
      <el-form :model="addform" :rules="addrule" ref="addFormRef">
        <el-form-item label="用户名" :label-width="formLabelWidth" prop="username">
          <el-input v-model="addform.username" autocomplete="off" size="large"></el-input>
        </el-form-item>
        <el-form-item label="密码" :label-width="formLabelWidth" prop="password">
          <el-input v-model="addform.password" autocomplete="off" size="large"></el-input>
        </el-form-item>
        <el-form-item label="邮箱" :label-width="formLabelWidth" prop="email">
          <el-input v-model="addform.email" autocomplete="off" size="large"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="addUser()">确 定</el-button>
      </div>
    </el-dialog>

    <!-- 修改用户界面 -->
    <el-dialog title="修改用户" :visible.sync="updateFormVisible" width="50%" @close="editclose" >
      <el-form :model="updateform" ref="updateFormRef" :rules="addrule" v-loading="userLoading">
        <el-form-item label="用户名" :label-width="formLabelWidth">
          <el-input
            :value="updateform.username"
            autocomplete="off"
            size="large"
            disabled="disabled"
          ></el-input>
        </el-form-item>
        <el-form-item label="密码" :label-width="formLabelWidth" prop="password">
          <el-input v-model="updateform.password" autocomplete="off" size="large"></el-input>
        </el-form-item>
        <el-form-item label="邮箱" :label-width="formLabelWidth" prop="email">
          <el-input v-model="updateform.email" autocomplete="off" size="large"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="updateFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="updateUser()">确 定</el-button>
      </div>
    </el-dialog>

  <!-- 修改用户权限界面 -->
    <el-dialog title="修改用户权限" :visible.sync="RoleFormVisible" width="50%" @close="Roleclose">
      <el-form :model="RoleForm" ref="RoleFormRef" :rules="addrule">
        <el-form-item label="用户权限" :label-width="formLabelWidth" prop="roles">
          <el-row>
            <el-col :span="8">
              <el-select v-model="RoleForm.roles" multiple placeholder="请选择">
                <el-option v-for="i in rolelist" :key="i.id" :label="i.role_des" :value="i.id"></el-option>
              </el-select>
            </el-col>
          </el-row>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="RoleFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="updateRole()">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import axios from 'axios';
export default {
  data() {
    var validateEmail = (rule, value, callback) => {
      const re = /^\w+@[a-z0-9]+\.[a-z]{2,4}$/;
      if (!re.test(value)) {
        callback(new Error("请输入正确邮箱"));
      } else {
        callback();
      }
    };
    var validateRoles = (rule, value, callback) => {
      console.log(this.RoleForm.roles);
      if (this.RoleForm.roles.length === 0) {
        callback(new Error("请选择用户角色"));
      } else {
        callback();
      }
    };
    return {
      userlist: [],
      rolelist: [],
      tableLoading:true,
      userLoading:true,
      pagenum: 1,
      pagesize: 5,
      totalpage: 0,
      searchname: "",
      dialogFormVisible: false,
      updateFormVisible: false,
      RoleFormVisible: false,
      formLabelWidth: "100px",
      addform: {
        username: "",
        password: "",
        email: ""
      },

      updateform: {
        username: "",
        password: "",
        email: "",
        id: ""
      },

      RoleForm: {
        roles: [],
        id: ""
      },

      addrule: {
        username: [
          { required: true, message: "请输入用户名", trigger: "blur" },
          { min: 3, max: 12, message: "长度在 3 到 12 个字符", trigger: "blur" }
        ],
        password: [
          { required: true, message: "请输入密码", trigger: "blur" },
          { min: 3, max: 12, message: "长度在 3 到 12 个字符", trigger: "blur" }
        ],
        email: [{ validator: validateEmail, trigger: "blur" }],
        roles: [{ validator: validateRoles, trigger: "change" }]
      }
    };
  },
  mounted: function() {
    this.loadUserList(this.pagenum, this.pagesize);
    this.loadRoleList();
  },
  methods: {
    addUser() {
      this.$refs.addFormRef.validate(async valid => {
        if (!valid) return;
        let param = new URLSearchParams();
        param.append("username", this.addform.username);
        param.append("password", this.addform.password);
        param.append("email", this.addform.email);
        await this.$http.post("/user/addUser", param).then(
          resp => {
            if (resp.status == 200 && resp.data.status == 1) {
              // console.log(resp);
              this.$refs.addFormRef.resetFields();
              this.loadUserList(this.pagenum, this.pagesize);
              this.dialogFormVisible = false;
              return this.$message.success(resp.data.msg);
            } else {
              return this.$message.error(resp.data.msg);
            }
          },
          resp => {
            return this.$message.error("未知错误");
          }
        );
      });
    },
    loadUserList(pagenum, pagesize) {
      let param = new URLSearchParams();
      param.append("pageSize", pagesize);
      param.append("pageNum", pagenum);
      this.$http.post("/user", param).then(
        resp => {
          this.userlist = resp.data.data.list;
          this.totalpage = resp.data.data.total;
          this.tableLoading=false;
        },
        resp => {
          alert("加载用户失败");
        }
      );
    },
    loadRoleList() {
      this.$http.get("/role/").then(
        resp => {
          this.rolelist = resp.data.data;
        },
        resp => {
          alert("加载用户权限失败");
        }
      );
    },
    handleSizeChange(newsize) {
      this.pagesize = newsize;
      if (this.searchname != "") {
        this.searchByName();
        return;
      }
      this.loadUserList(this.pagenum, this.pagesize);
    },
    handleCurrentChange(newnum) {
      this.pagenum = newnum;
      if (this.searchname != "") {
        this.searchByName();
        return;
      }
      this.loadUserList(this.pagenum, this.pagesize);
    },
    statusChange(newstatus, userid) {
      var userstatus = 0;
      if (newstatus == true) {
        userstatus = 1;
      }
      let param = new URLSearchParams();
      param.append("userid", userid);
      param.append("userstatus", userstatus);
      this.$http.post("/user/changeUserStatus", param).then(
        resp => {
          if (resp.status == 200 && resp.data.status == 1) {
            // console.log(resp);
            return this.$message.success(resp.data.msg);
          } else {
            return this.$message.error(resp.data.msg);
          }
        },
        resp => {
          return this.$message.error("未知错误");
        }
      );
    },
    searchByName() {
      let param = new URLSearchParams();
      param.append("pageSize", this.pagesize);
      param.append("pageNum", this.pagenum);
      param.append("str", this.searchname);
      this.$http.post("/user/searchUser", param).then(
        resp => {
          if (resp.status == 200 && resp.data.status == 1) {
            this.userlist = resp.data.data.list;
            this.totalpage = resp.data.data.total;
          } else {
            this.$message.error(resp.data.msg);
          }
        },
        resp => {
          alert("搜索失败");
        }
      );
    },
    delUser(id) {
      this.$confirm("删除该用户,该操作不可逆, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          let param = new URLSearchParams();
          param.append("uid", id);
          this.$http.post("/user/delUser", param).then(
            resp => {
              if (resp.status == 200 && resp.data.status == 1) {
                this.$message({ type: "success", message: "删除成功!" });
                this.loadUserList(this.pagenum, this.pagesize);
                return;
              }
              this.$message({ type: "error", message: "删除失败!" });
            },
            resp => {
              this.$message({ type: "error", message: "删除失败!" });
            }
          );
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除"
          });
        });
    },
    updateUser(uid) {
      this.$refs.updateFormRef.validate(async valid => {
        if (!valid) return;
        let param = new URLSearchParams();
        param.append("uid", this.updateform.id);
        param.append("password", this.updateform.password);
        param.append("email", this.updateform.email);
        await this.$http.post("/user/updateUser", param).then(
          resp => {
            if (resp.status == 200 && resp.data.status == 1) {
              this.$refs.updateFormRef.resetFields();
              this.loadUserList(this.pagenum, this.pagesize);
              this.updateFormVisible = false;
              return this.$message.success(resp.data.msg);
            } else {
              return this.$message.error(resp.data.msg);
            }
          },
          resp => {
            return this.$message.error("未知错误");
          }
        );
      });
    },
    showUpdateUser(uid) {
      var _this = this;
      this.updateFormVisible = true;
      this.$http
        .get("/user/findById", {
          params: {
            uid: uid
          }
        })
        .then(
          resp => {
            this.updateform.password = resp.data.data.password;
            this.updateform.email = resp.data.data.email;
            this.updateform.username = resp.data.data.username;
            this.updateform.id = resp.data.data.id;
            this.userLoading=false;
          },
          resp => {
            alert("加载添加用户模块失败");
          }
        );
    },
    showEditRole(id) {
      var _this = this;
      this.RoleFormVisible = true;
      var url = "/role/" + id;
      this.$http.get(url).then(
        resp => {
          this.RoleForm.id = id;
          for (var i = 0; i < resp.data.data.length; i++) {
            _this.RoleForm.roles.push(resp.data.data[i].id);
          }
        },
        resp => {
          alert("加载角色模块失败");
        }
      );
    },
    updateRole() {
        let param = new URLSearchParams();
        param.append("roles", this.RoleForm.roles);
        param.append("id", this.RoleForm.id);
      this.$http.post("/user/updateRoles",param).then(
          resp => {
            if (resp.status == 200 && resp.data.status == 1) {
              this.$refs.RoleFormRef.resetFields();
              this.loadUserList(this.pagenum, this.pagesize);
              this.RoleFormVisible = false;
              return this.$message.success(resp.data.msg);
            } else {
              return this.$message.error(resp.data.msg);
            }
          },
          resp => {
            return this.$message.error("未知错误");
          }
      )
    },
    editclose() {
      this.$refs.updateFormRef.resetFields();
    },
    addclose() {
      this.$refs.addFormRef.resetFields();
    },
    Roleclose() {
      this.$refs.RoleFormRef.resetFields();
    }
  }
};
</script>

<style>
.el-pagination {
  margin-top: 15px;
}

.el-row {
  margin-bottom: 20px;
}
</style>