<template>
  <el-container class="home_container">
    <el-header class="el-header">
      Test后台管理
      <el-button type="info" @click="loginOut">退出</el-button>
    </el-header>
    <el-container>
      <el-aside :width="flag1 ? '64px' : '200px'" class="el-aside">
        <div class="toggle-button" @click="toggleMenu">|||</div>
        <el-menu
          router
          :unique-opened="true"
          :collapse="flag1"
          :collapse-transition="false"
          background-color="#474747"
          text-color="#fff"
          active-text-color="#20a0ff"
        >
          <template v-for="(item,index) in this.$router.options.routes" v-if="!item.hidden">
            <el-submenu :index="index+''" :key="index">
              <template slot="title">
                <i :class="item.icon"></i>
                <span>{{item.name}}</span>
              </template>
              <el-menu-item
                v-for="child in item.children"
                v-if="!child.hidden"
                :index="child.path"
                :key="child.path"
                background-color="#fff"
              >
                <template>
                  <i :class="child.icon" :key="child.path"></i>
                  <span>{{child.name}}</span>
                </template>
              </el-menu-item>
            </el-submenu>
          </template>
        </el-menu>
      </el-aside>
      <el-main class="el-main">
        <el-breadcrumb separator-class="el-icon-arrow-right">
          <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
          <el-breadcrumb-item v-text="this.$router.currentRoute.name"></el-breadcrumb-item>
        </el-breadcrumb>

          <router-view></router-view>

      </el-main>
    </el-container>
  </el-container>
</template>

<script>
export default {
  methods: {
    loginOut: function() {
      window.sessionStorage.clear();
      this.$router.push("/login");
      
    },
    toggleMenu() {
      this.flag1 = !this.flag1;
    }
  },
  data() {
    return {
      flag1: false
    };
  }
};
</script>

<style lang="less">
.home_container {
  height: 100%;
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
}

.el-header {
  background-color: #20a0ff;
  color: #333;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-left: 0px;
  color: #fff;
  font-size: 150%;
}

.el-aside {
  background-color: #474747;
  .el-menu {
    border-right: none;
  }
}

.el-main {
  background-color: #fff;
  color: #000;
  text-align: center;
}

.toggle-button {
  font-size: 100%;
  text-align: center;
  letter-spacing: 0.2em;
  cursor: pointer;
  color: #fff;
  background-color: black;
}
.el-breadcrumb{
  padding-bottom: 20px;
}
</style>