<template>
  <div>
    <el-card class="box-card">
      <el-button type="primary" size="small" id="button1" @click="addRoleFormVisible=true">添加角色</el-button>
      <el-button type="primary" size="small"  @click="loadRoleList">刷新</el-button>
      <el-table :data="rolelist" stripe style="width: 100%">
        <el-table-column prop="id" label="角色ID" width="200" align="center"></el-table-column>
        <el-table-column prop="role_name" label="角色名称" width="400" align="center"></el-table-column>
        <el-table-column prop="role_des" label="角色描述" width="400" align="center"></el-table-column>
        <el-table-column fixed="right" label="操作" width="180" align="center">
          <template slot-scope="scope">
            <el-tooltip
              class="item"
              effect="dark"
              content="编辑角色"
              placement="top"
              :enterable="false"
            >
              <el-button
                type="primary"
                icon="el-icon-edit"
                circle
                @click="showEditRole(scope.row.id)"
              ></el-button>

            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>
    </el-card>

    <el-dialog title="修改角色" :visible.sync="RoleFormVisible" width="50%" @close="editclose">
      <el-form :model="Roleform" ref="RoleFormRef" :rules="Rolerule">
        <el-form-item label="ID" :label-width="formLabelWidth">
          <el-input :value="Roleform.id" autocomplete="off" size="large" disabled="disabled"></el-input>
        </el-form-item>
        <el-form-item label="角色名称" :label-width="formLabelWidth" prop="role_name">
          <el-input v-model="Roleform.role_name" autocomplete="off" size="large"></el-input>
        </el-form-item>
        <el-form-item label="角色描述" :label-width="formLabelWidth" prop="role_des">
          <el-input v-model="Roleform.role_des" autocomplete="off" size="large"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="RoleFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="updateRole()">确 定</el-button>
      </div>
    </el-dialog>

    <el-dialog title="添加角色" :visible.sync="addRoleFormVisible" width="50%" @close="addeditclose">
      <el-form :model="addRoleform" ref="addRoleFormRef" :rules="Rolerule">
        <el-form-item name="name" label="角色名称" :label-width="formLabelWidth" prop="role_name">
          <el-input v-model="addRoleform.role_name" autocomplete="off" size="large"></el-input>
        </el-form-item>
        <el-form-item name="desc" label="角色描述" :label-width="formLabelWidth" prop="role_des">
          <el-input v-model="addRoleform.role_des" autocomplete="off" size="large"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="addRoleFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="addRole()">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
export default {
  data() {
    return {
      socket: "",
      formLabelWidth: "120",
      rolelist: [],
      RoleFormVisible: false,
      addRoleFormVisible: false,
      Roleform: {
        id: "",
        role_name: "",
        role_des: ""
      },
      addRoleform: {
        role_name: "",
        role_des: ""
      },

      Rolerule: {
        role_name: [
          { required: true, message: "请输入角色名称", trigger: "blur" }
        ],
        role_des: [
          { required: true, message: "请输入角色描述", trigger: "blur" }
        ]
      }
    };
  },
  methods: {
    loadRoleList() {
      this.$http.get("/role/").then(
        resp => {
          console.log("loadlist")
          this.rolelist = resp.data.data;
          console.log(this.rolelist)
        },
        resp => {
          alert("加载用户权限失败");
        }
      );
    },
    showEditRole(id) {
      this.RoleFormVisible = true;
      this.$http
        .get("role/findRoleById", {
          params: {
            rid: id
          }
        })
        .then(
          resp => {
            this.Roleform.id = resp.data.data.id;
            this.Roleform.role_name = resp.data.data.role_name;
            this.Roleform.role_des = resp.data.data.role_des;
          },
          resp => {
            alert("加载模块失败");
          }
        );
    },
    updateRole() {
      this.$refs.RoleFormRef.validate(async valid => {
        if (!valid) return;
        var param = new URLSearchParams();
        param.append("id", this.Roleform.id);
        param.append("role_name", this.Roleform.role_name);
        param.append("role_des", this.Roleform.role_des);
        await this.$http.post("/role/updateRole", param).then(
          resp => {
            if (resp.status == 200 && resp.data.status == 1) {
              this.$refs.RoleFormRef.resetFields();
              // this.loadRoleList();
              this.socket.send("ok");
              this.RoleFormVisible = false;
              return this.$message.success(resp.data.msg);
            } else {
              return this.$message.error(resp.data.msg);
            }
          },
          reps => {
            return this.$message.error("error");
          }
        );
      });
    },
    addRole() {
      this.$refs.addRoleFormRef.validate(async valid => {
        if (!valid) return;
        var param = new URLSearchParams();
        param.append("name", this.addRoleform.role_name);
        param.append("desc", this.addRoleform.role_des);
        await this.$http.post("/role/addRole", param).then(
          resp => {
            if (resp.status == 200 && resp.data.status == 1) {
              this.loadRoleList();
              this.addRoleFormVisible = false;
              return this.$message.success(resp.data.msg);
            } else {
              return this.$message.error(resp.data.msg);
            }
          },
          resp => {
            return this.$message.error("未知错误");
          }
        );
      });
    },
    editclose() {
      this.$refs.RoleFormRef.resetFields();
    },
    addeditclose() {
      this.$refs.addRoleFormRef.resetFields();
    },
    open() {
      console.log("sokect open");
    },
    close() {
      console.log("sokect close");
    },
    websocketonmessage(e){
      console.log(e)
      if(e.data=="flush1"){
        this.loadRoleList();
      }
    }
  },
  mounted: function() {
    var url="ws://localhost//productWebSocket/test"
    this.socket = new WebSocket(url);
    this.socket.onopen = this.open;
    this.socket.onclose = this.close;
    this.socket.onmessage = this.websocketonmessage;
    this.loadRoleList();
  },
  destroyed(){
    this.socket.close();
  }
};
</script>

<style>
#button1 {
  float: right;
}
</style>